import React from 'react';
import styles from './buisnesstime.module.css';

const BuisnessTime = ({time}) => {
  return (
    <div  className={styles.container}>
      {!time && (<div>  </div>)}
      {time > 0 && time < 60 && (<div>{`${time} `}</div>)}
      {time >= 60 && time < 120 && (<div>{`${Math.floor(time / 60)}  ${time % 60} `}</div>)}
      {time >= 120 && time < 300 && (<div>{`${Math.floor(time / 60)}  ${time % 60} `}</div>)}
      {time >= 300 && (<div>{`${Math.floor(time / 60)}  ${time % 60} `}</div>)}
    </div>
  );
};

export default BuisnessTime;
